'use client'

import { useEffect, useState } from 'react'
import { ChevronDown, ChevronRight, Plus, X } from 'lucide-react'

export default function VinculacaoOrganizacionalPage() {
  const [data, setData] = useState<any[]>([])
  const [openDptos, setOpenDptos] = useState<number[]>([])
  const [openFuncoes, setOpenFuncoes] = useState<number[]>([])
  const [showModal, setShowModal] = useState(false)

  const [departamentos, setDepartamentos] = useState<any[]>([])
  const [funcoes, setFuncoes] = useState<any[]>([])
  const [usuarios, setUsuarios] = useState<any[]>([])

  const [form, setForm] = useState({
    dpto_id: '',
    func_id: '',
    user_id: '',
    percentual_alocacao: 100,
  })

  useEffect(() => {
    carregarHierarquia()
    carregarCombos()
  }, [])

  async function carregarHierarquia() {
    const token = localStorage.getItem('entity_token')
    const res = await fetch(
      `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao`,
      {
        headers: { Authorization: `Bearer ${token}` },
      },
    )
    setData(await res.json())
  }

  async function carregarCombos() {
    const token = localStorage.getItem('entity_token')

    const res = await fetch(
      `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao/combos`,
      {
        headers: { Authorization: `Bearer ${token}` },
      },
    )

    const json = await res.json()

    setDepartamentos(json.departamentos || [])
    setFuncoes(json.funcoes || [])
    setUsuarios(json.usuarios || [])
  }

  async function salvar() {
    const token = localStorage.getItem('entity_token')

    await fetch(
      `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao`,
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(form),
      },
    )

    setShowModal(false)
    setForm({
      dpto_id: '',
      func_id: '',
      user_id: '',
      percentual_alocacao: 100,
    })

    carregarHierarquia()
  }

  function toggle(list: number[], id: number, setter: any) {
    setter(
      list.includes(id)
        ? list.filter(i => i !== id)
        : [...list, id],
    )
  }

  return (
    <div>
      {/* HEADER */}
      <div style={header}>
        <h1>Vinculação Organizacional</h1>

        <button style={btnNovo} onClick={() => setShowModal(true)}>
          <Plus size={14} /> Novo vínculo
        </button>
      </div>

      {/* ÁRVORE */}
      {data.map(d => (
        <div key={d.dpto_id} style={{ marginBottom: 12 }}>
          <div
            style={dptoRow}
            onClick={() => toggle(openDptos, d.dpto_id, setOpenDptos)}
          >
            {openDptos.includes(d.dpto_id)
              ? <ChevronDown size={16} />
              : <ChevronRight size={16} />}
            {d.nome}
          </div>

          {openDptos.includes(d.dpto_id) &&
            d.funcoes.map(f => (
              <div key={f.func_id} style={{ marginLeft: 24 }}>
                <div
                  style={funcRow}
                  onClick={() =>
                    toggle(openFuncoes, f.func_id, setOpenFuncoes)
                  }
                >
                  {openFuncoes.includes(f.func_id)
                    ? <ChevronDown size={14} />
                    : <ChevronRight size={14} />}
                  {f.nome}
                </div>

                {openFuncoes.includes(f.func_id) &&
                  f.usuarios.map((u: any) => (
                    <div key={u.user_id} style={userRow}>
                      {u.nome} ({u.percentual_alocacao}%)
                    </div>
                  ))}
              </div>
            ))}
        </div>
      ))}

      {/* MODAL */}
      {showModal && (
        <div style={overlay}>
          <div style={modal}>
            <div style={modalHeader}>
              <h2>Novo vínculo</h2>
              <X style={{ cursor: 'pointer' }} onClick={() => setShowModal(false)} />
            </div>

            <div style={modalBody}>
              <select
                value={form.dpto_id}
                onChange={e => setForm({ ...form, dpto_id: e.target.value })}
                style={input}
              >
                <option value="">Departamento</option>
                {departamentos.map(d => (
                  <option key={d.dpto_id} value={d.dpto_id}>
                    {d.nome}
                  </option>
                ))}
              </select>

              <select
                value={form.func_id}
                onChange={e => setForm({ ...form, func_id: e.target.value })}
                style={input}
              >
                <option value="">Função</option>
                {funcoes.map(f => (
                  <option key={f.func_id} value={f.func_id}>
                    {f.nome}
                  </option>
                ))}
              </select>

              <select
                value={form.user_id}
                onChange={e => setForm({ ...form, user_id: e.target.value })}
                style={input}
              >
                <option value="">Usuário</option>
                {usuarios.map(u => (
                  <option key={u.user_id} value={u.user_id}>
                    {u.nome}
                  </option>
                ))}
              </select>

              <input
                type="number"
                value={form.percentual_alocacao}
                onChange={e =>
                  setForm({
                    ...form,
                    percentual_alocacao: Number(e.target.value),
                  })
                }
                style={input}
              />
            </div>

            <div style={modalFooter}>
              <button style={btnCancelar} onClick={() => setShowModal(false)}>
                Cancelar
              </button>
              <button style={btnSalvar} onClick={salvar}>
                Salvar
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}

/* ===== STYLES ===== */

const header = {
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  marginBottom: 24,
}

const btnNovo = {
  display: 'inline-flex',
  alignItems: 'center',
  gap: 6,
  background: '#16a34a',
  color: '#fff',
  padding: '6px 12px',
  height: 36,
  borderRadius: 8,
  border: 'none',
  cursor: 'pointer',
  fontSize: 14,
  fontWeight: 500,
}

const dptoRow = {
  display: 'flex',
  alignItems: 'center',
  gap: 6,
  fontWeight: 600,
  cursor: 'pointer',
}

const funcRow = {
  display: 'flex',
  alignItems: 'center',
  gap: 6,
  cursor: 'pointer',
}

const userRow = {
  marginLeft: 44,
  fontSize: 13,
}

const overlay = {
  position: 'fixed' as const,
  inset: 0,
  background: 'rgba(0,0,0,0.4)',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  zIndex: 50,
}

const modal = {
  background: '#fff',
  width: 420,
  borderRadius: 12,
  overflow: 'hidden',
}

const modalHeader = {
  padding: '16px 20px',
  borderBottom: '1px solid #e5e7eb',
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
}

const modalBody = {
  padding: 20,
  display: 'flex',
  flexDirection: 'column' as const,
  gap: 12,
}

const modalFooter = {
  padding: '12px 20px',
  borderTop: '1px solid #e5e7eb',
  display: 'flex',
  justifyContent: 'flex-end',
  gap: 10,
}

const input = {
  padding: '10px 12px',
  borderRadius: 8,
  border: '1px solid #d1d5db',
}

const btnCancelar = {
  padding: '8px 14px',
  borderRadius: 8,
  border: 'none',
  background: '#e5e7eb',
  cursor: 'pointer',
}

const btnSalvar = {
  padding: '8px 14px',
  borderRadius: 8,
  border: 'none',
  background: '#16a34a',
  color: '#fff',
  cursor: 'pointer',
}
